'use client';

import { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Modal, ConfirmDialog } from '@/components/ui/modal';
import { mockNurses } from '@/lib/mock-data';
import { formatDate } from '@/lib/utils';
import { 
  UserCheck, 
  Search, 
  Plus, 
  Eye, 
  Edit, 
  MessageSquare, 
  X,
  Phone,
  Mail,
  MapPin,
  Calendar,
  Clock,
  Star
} from 'lucide-react';

export default function NursesPage() {
  const [searchQuery, setSearchQuery] = useState('');
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [isViewModalOpen, setIsViewModalOpen] = useState(false);
  const [selectedNurse, setSelectedNurse] = useState<any>(null);

  // Use mock data instead of API calls
  const nurses = mockNurses;
  const isLoading = false;

  const handleAddNurse = () => {
    setSelectedNurse(null);
    setIsAddModalOpen(true);
  };

  const handleEditNurse = (nurse: any) => {
    setSelectedNurse(nurse);
    setIsEditModalOpen(true);
  };

  const handleViewNurse = (nurse: any) => {
    setSelectedNurse(nurse);
    setIsViewModalOpen(true);
  };

  const handleDeleteNurse = (nurse: any) => {
    setSelectedNurse(nurse);
    setIsDeleteDialogOpen(true);
  };

  const filteredNurses = nurses.filter(nurse => {
    const matchesSearch = 
      nurse.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      nurse.specialty.toLowerCase().includes(searchQuery.toLowerCase()) ||
      nurse.email.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesSearch;
  });

  if (isLoading) {
    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-900">Nurses</h1>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {[1, 2, 3].map((i) => (
            <Card key={i}>
              <CardContent className="p-6">
                <div className="animate-pulse">
                  <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                  <div className="h-8 bg-gray-200 rounded w-1/2"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Nurses</h1>
        <Button onClick={handleAddNurse} variant="primary" size="lg" className="shadow-lg">
          <Plus className="h-5 w-5 mr-2" />
          Add Nurse
        </Button>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-blue-100 rounded-lg">
                <UserCheck className="h-6 w-6 text-blue-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Total Nurses</p>
                <p className="text-2xl font-bold text-gray-900">{nurses.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-green-100 rounded-lg">
                <Clock className="h-6 w-6 text-green-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Active Nurses</p>
                <p className="text-2xl font-bold text-gray-900">
                  {nurses.filter(n => n.status === 'active').length}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-purple-100 rounded-lg">
                <Star className="h-6 w-6 text-purple-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Specialties</p>
                <p className="text-2xl font-bold text-gray-900">
                  {new Set(nurses.map(n => n.specialty)).size}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Search */}
      <div className="flex items-center space-x-4">
        <div className="relative flex-1 max-w-md">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
          <Input
            placeholder="Search nurses..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-10"
          />
        </div>
      </div>

      {/* Nurses List */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredNurses.map((nurse) => (
          <Card key={nurse.id} className="hover:shadow-lg transition-shadow">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <CardTitle className="text-lg">{nurse.name}</CardTitle>
                <Badge 
                  variant={nurse.status === 'active' ? 'success' : 'destructive'}
                >
                  {nurse.status}
                </Badge>
              </div>
              <CardDescription className="flex items-center">
                <UserCheck className="h-4 w-4 mr-1" />
                {nurse.specialty}
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="flex items-center text-sm text-gray-600">
                <Mail className="h-4 w-4 mr-2" />
                {nurse.email}
              </div>
              <div className="flex items-center text-sm text-gray-600">
                <Phone className="h-4 w-4 mr-2" />
                {nurse.phone}
              </div>
              <div className="flex items-center text-sm text-gray-600">
                <Calendar className="h-4 w-4 mr-2" />
                {nurse.experience}
              </div>
              <div className="flex items-center text-sm text-gray-600">
                <Clock className="h-4 w-4 mr-2" />
                License: {nurse.licenseNumber}
              </div>
              <div className="flex space-x-2 pt-2">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => handleViewNurse(nurse)}
                  className="flex-1"
                >
                  <Eye className="h-4 w-4 mr-1" />
                  View
                </Button>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => handleEditNurse(nurse)}
                  className="flex-1"
                >
                  <Edit className="h-4 w-4 mr-1" />
                  Edit
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Empty State */}
      {filteredNurses.length === 0 && (
        <Card>
          <CardContent className="p-12 text-center">
            <UserCheck className="h-12 w-12 text-gray-400 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">No nurses found</h3>
            <p className="text-gray-600 mb-4">
              {searchQuery ? 'Try adjusting your search terms.' : 'Get started by adding a new nurse.'}
            </p>
            {!searchQuery && (
              <Button onClick={handleAddNurse} variant="primary">
                <Plus className="h-4 w-4 mr-2" />
                Add Nurse
              </Button>
            )}
          </CardContent>
        </Card>
      )}

      {/* Modals - Placeholder for now */}
      {isAddModalOpen && (
        <Modal
          isOpen={isAddModalOpen}
          onClose={() => setIsAddModalOpen(false)}
          title="Add New Nurse"
        >
          <div className="p-6">
            <p className="text-gray-600">Nurse registration form will be implemented here.</p>
            <div className="flex justify-end space-x-3 mt-6">
              <Button variant="outline" onClick={() => setIsAddModalOpen(false)}>
                Cancel
              </Button>
              <Button variant="primary" onClick={() => setIsAddModalOpen(false)}>
                Add Nurse
              </Button>
            </div>
          </div>
        </Modal>
      )}

      {isViewModalOpen && selectedNurse && (
        <Modal
          isOpen={isViewModalOpen}
          onClose={() => setIsViewModalOpen(false)}
          title="Nurse Details"
        >
          <div className="p-6 space-y-4">
            <div>
              <h3 className="font-medium text-gray-900">Name</h3>
              <p className="text-gray-600">{selectedNurse.name}</p>
            </div>
            <div>
              <h3 className="font-medium text-gray-900">Email</h3>
              <p className="text-gray-600">{selectedNurse.email}</p>
            </div>
            <div>
              <h3 className="font-medium text-gray-900">Phone</h3>
              <p className="text-gray-600">{selectedNurse.phone}</p>
            </div>
            <div>
              <h3 className="font-medium text-gray-900">Specialty</h3>
              <p className="text-gray-600">{selectedNurse.specialty}</p>
            </div>
            <div>
              <h3 className="font-medium text-gray-900">Experience</h3>
              <p className="text-gray-600">{selectedNurse.experience}</p>
            </div>
            <div>
              <h3 className="font-medium text-gray-900">License Number</h3>
              <p className="text-gray-600">{selectedNurse.licenseNumber}</p>
            </div>
            <div>
              <h3 className="font-medium text-gray-900">Status</h3>
              <Badge 
                variant={selectedNurse.status === 'active' ? 'success' : 'destructive'}
              >
                {selectedNurse.status}
              </Badge>
            </div>
            <div className="flex justify-end space-x-3 mt-6">
              <Button variant="outline" onClick={() => setIsViewModalOpen(false)}>
                Close
              </Button>
              <Button variant="primary" onClick={() => {
                setIsViewModalOpen(false);
                handleEditNurse(selectedNurse);
              }}>
                Edit Nurse
              </Button>
            </div>
          </div>
        </Modal>
      )}

      {isEditModalOpen && selectedNurse && (
        <Modal
          isOpen={isEditModalOpen}
          onClose={() => setIsEditModalOpen(false)}
          title="Edit Nurse"
        >
          <div className="p-6">
            <p className="text-gray-600">Nurse editing form will be implemented here.</p>
            <div className="flex justify-end space-x-3 mt-6">
              <Button variant="outline" onClick={() => setIsEditModalOpen(false)}>
                Cancel
              </Button>
              <Button variant="primary" onClick={() => setIsEditModalOpen(false)}>
                Save Changes
              </Button>
            </div>
          </div>
        </Modal>
      )}

      {isDeleteDialogOpen && selectedNurse && (
        <ConfirmDialog
          isOpen={isDeleteDialogOpen}
          onClose={() => setIsDeleteDialogOpen(false)}
          onConfirm={() => {
            // Handle delete logic here
            setIsDeleteDialogOpen(false);
          }}
          title="Delete Nurse"
          message={`Are you sure you want to delete ${selectedNurse.name}? This action cannot be undone.`}
        />
      )}
    </div>
  );
}